<template>
  <div class="post-wrap">
    <h3>轉盤功能介紹</h3>

    <h4>drag 圓盤的時候，圓盤會旋轉。</h4>
    <ul>
        <li>drag 從左到右：圓盤逆時針旋轉。</li>
        <li>drag 從右到左：圓盤順時針旋轉。</li>
    </ul>

    <h4>電腦版有左右箭頭，可以改變圓盤圖的 deg，讓它旋轉。</h4>
    <ul>
        <li>右箭頭：圓盤順時針旋轉。</li>
        <li>左箭頭：圓盤逆時針旋轉。</li>
    </ul>

    <h4>滑鼠/觸控板的滾輪往上往下，可以改變圓盤圖的 deg，讓它旋轉。</h4>
    <ul>
        <li>滾輪往下：圓盤順時針旋轉。</li>
        <li>滾輪往上：圓盤逆時針旋轉。</li>
    </ul>

    <h4>限制旋轉範圍：</h4>
    <ul>
        <li>Level 1：以起始點為中心，正負 30 度可旋轉。</li>
        <li>Level 2：以起始點為中心，正負 60 度可旋轉。</li>
        <li>Level 3：以起始點為中心，正負 90 度可旋轉。</li>
        <li>Level 4：以起始點為中心，正負 120 度可旋轉。</li>
        <li>Level 5：以起始點為中心，正負 150 度可旋轉。</li>
        <li>Level 6：正負 360 度可旋轉。</li>
    </ul>




    <h3>電腦版</h3>

    <h4>起始畫面（網頁錄影）</h4>
    <div class="device-wrap video-container">
        <iframe width="560" height="315" src="https://www.youtube.com/embed/wSeAg-H3_4I" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    </div>


    <h4>起始畫面（截圖）</h4>
    <div class="device-wrap pc-dev-wrap" style="--device-w:1920; --device-h:1080;">
        <div class="dev-outter-wrap">
            <div class="dev-inner-wrap">           
                <img src="@/assets/images/work/dining_in_the_dark_3/start_1920x1080.png" />
            </div>
        </div>
    </div>


    <h3>手機版</h3>

    <h4>起始畫面（截圖）</h4>
    <div class="col-1-mobile-wrap">
        <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
            <div class="dev-outter-wrap">
                <div class="dev-inner-wrap">
                    <img src="@/assets/images/work/dining_in_the_dark_3/start_375x667.png" />
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'FemDiningInTheDark3',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

